.slogan {
    display: flex;
    width: 30%;
    height: 20%;
    padding: 10%;
    margin-inline: 5%;
    font-size: min(30px , 4vw);
    color: #1a1c1a;
}

.section-header {
    margin-inline: 5%;
    color: #596054;
    height: 5vh;
    width: 100%;
    display: 'flex';
    justify-content: 'center';
    align-items: 'center';
}

.about-us {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 40%;
    margin-inline: 5%;
    height: auto;
    font-size: min(14px , 6vw);
    color: #596054;
}
@media (max-width: 480px) {
    .about-us{
        width: 90%;
    }
}

.project-card{
    height: 70%;
    aspect-ratio: 16/9;
}