.nav {
    position: fixed;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100vw;
    height: 8vh;
    background-image: linear-gradient(120deg, #d6d6d6 0%, #929292 100%)
}


.logo-container {
    width: 20vw;
    padding-inline: 1rem;

}

.logo-container img {
    width: 200px;
}
.social-media-icons {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 15rem;
    padding-inline: 1rem;
    position: relative;

}
.social-media-icons a {
    color: #232323;
    font-size: 2rem
}
.nav-button-container {
    z-index: 20;
    position: relative;
    right: 3rem;
    bottom: 25%;
}
